import React, { useState } from "react";
import styles from "./lightswitch.module.css";
import classNames from "classnames";

function LightSwitch({ click }) {
  const [isAnimating, setIsAnimating] = useState(false);

  const handleClick = () => {
    // Temporarily remove the animation class to reset it
    setIsAnimating(true);

    // Add the class back after a short delay to trigger animation
    setTimeout(() => {
      setIsAnimating(false);
    }, 1000);

    // Call the dark mode toggle function
    click()
  };

  return (
    <div className={styles.lightSwitchContainer}>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 100 300"
        className={`${styles.lightSwitchSVG} ${isAnimating ? styles.clicked : ""}`}
        onClick={handleClick}
      >
        {/* Wire */}
        <line
          x1="50"
          y1="0"
          x2="50"
          y2="250"
          stroke="currentColor"
          strokeWidth="2"
        />

        {/* Pear-shaped Switch */}
        <path
          d="M40 250
             C40 270, 60 270, 60 250
             C60 230, 40 230, 40 250
             Z"
          fill="currentColor"
        />
      </svg>
    </div>
  );
}

export default LightSwitch;
